<template>
  <div class="pa-4">
    <h1>JS Closures</h1>
    <CodeHighlight :code="code" />
    <v-btn @click="runClosure">
      运行
    </v-btn>
    <p>将函数作为返回值，就会看到闭包在这些函数中的应用</p>
  </div>
</template>

<script>
import CodeHighlight from '@/modules/codehl/CodeHighlight.vue';


export default {
  components: {
    CodeHighlight,
  },
  data() {
    return {
      code: `function foo() {
  var a = "abc";
  function bar() {
    console.log(a);
  }
  return bar;
}

var baz = foo();
baz(); // abc`,
    }
  },

  methods: {
    closure() {
      const a = 'abc';
      function fn() {
        return a;
      }
      return fn();
    },
    runClosure() {
      const a = this.closure();
      console.log(a);
    },
  },
}
</script>

