  },
  computed: {
    isDarkMode() {
      return this.$vuetify.theme.dark;
    },
    codeText() {
      return this.code;
    }
  },

  mounted() {
    // 在组件挂载后初始化代码高亮
    // 使用 highlight.js 对代码进行高亮
    hljs.highlightBlock(this.$refs.codeElement);
  },

  methods: {
    changeLanguage() {
      const codeElement = this.$refs.codeElement;
      codeElement.removeAttribute('data-highlighted');
      this.languageclass = `language-${this.language}`;
      // hljs.highlightElement(this.$refs.codeElement);
      hljs.highlightBlock(this.$refs.codeElement);
      this.isHighlight = 'yes';
